.container{
    width: 100%;
    height: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
}
.hero {
    display: flex;
    justify-content: end;
    margin-left: 5rem;
}

.hero-left {
    flex: 1;
    align-self: center;
}

.hero-left>img{
    align-items: end;
}

.hero-right {
    flex: 1;
    align-items: center;
}
.hero-scroll{
    text-align: center;
    display: block;
    justify-content: center;
    align-items: center;
}